




































































import Vue from 'vue';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import { faClock } from '@fortawesome/free-regular-svg-icons';

import {
  Alert,
  Button,
  Checkbox,
  Dialog,
  Form,
  FormItem,
  Input,
  InputNumber,
  Option,
  Select,
} from 'element-ui';
import { getKey, updateTtl } from '@/services/dynomite/DynoService';
import { notify } from '@/utils/message-utils';
import { NotificationType } from '@/typings/notifications';

export default Vue.extend({
  name: 'DynoKeyTtlEditor',
  components: {
    [Alert.name]: Alert,
    [Button.name]: Button,
    [Checkbox.name]: Checkbox,
    [Dialog.name]: Dialog,
    [Form.name]: Form,
    [FormItem.name]: FormItem,
    [Input.name]: Input,
    [InputNumber.name]: InputNumber,
    [Option.name]: Option,
    [Select.name]: Select,
    FontAwesomeIcon,
  },
  props: {
    clusterName: {
      type: String,
      required: true,
    },
    keyName: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      faClock,
      isLoading: false,
      ttlForm: {
        expires: false,
        time: 0,
        units: 'seconds' as 'seconds' | 'minutes' | 'hours',
      },
      units: ['seconds', 'minutes', 'hours'],
      showExpirationWarning: false,
    };
  },
  computed: {
    rules(): any {
      return {
        time: [
          {
            required: true,
            message: 'An expiration duration must be specified',
          },
          { type: 'number', message: 'Expiration must be a valid number' },
        ],
      };
    },
    ttlValue: {
      get(): number {
        const { expires, time, units } = this.ttlForm;
        if (!expires) return -1;
        let value = 0;
        switch (units) {
          case 'hours':
            value = time * 60 * 60;
            break;
          case 'minutes':
            value = time * 60;
            break;
          default:
            value = time;
        }
        return value;
      },
      set(value: number) {
        let expires = false;
        let time = 0;
        let units = 'seconds' as 'seconds' | 'minutes' | 'hours';
        if (value && value >= 0) {
          expires = true;
          if (value % (60 * 60) === 0) {
            time = value / (60 * 60);
            units = 'hours';
          } else if (value % 60 === 0) {
            time = value / 60;
            units = 'minutes';
          } else {
            time = value;
            units = 'seconds';
          }
        }
        this.ttlForm = {
          expires,
          time,
          units,
        };
      },
    },
  },
  async created() {
    this.isLoading = true;
    try {
      const key = await getKey(this.clusterName, this.keyName);
      this.ttlValue = key.ttl;
    } catch (err) {
      notify(
        NotificationType.Error,
        'Failed to Fetch Key Expiration',
        err.message,
      );
    } finally {
      this.isLoading = false;
    }
  },
  methods: {
    onClose() {
      this.$emit('close');
    },
    async onSubmit() {
      if (!(await (this.$refs.form as any).validate())) {
        return;
      }
      this.isLoading = true;
      try {
        await updateTtl(this.clusterName, this.keyName, this.ttlValue);
        notify(
          NotificationType.Success,
          'Updated Expiration',
          `Updated expiration of ${this.keyName}`,
        );
        this.$emit('updated-ttl');
      } catch (err) {
        notify(
          NotificationType.Error,
          'Failed to Update Expiration',
          err.message,
        );
      } finally {
        this.isLoading = false;
      }
    },
  },
});
