






















import Vue from 'vue';
import { Alert, Form, FormItem, Input } from 'element-ui';
import { IKeyValue } from '@dynomitetypes/dynomite';
import { getKey } from '@/services/dynomite/DynoService';
import DynoKeyDetails from '@/components/dynomite/DynoKeyDetails.vue';

export default Vue.extend({
  name: 'DynoEditKeyView',
  components: {
    [Alert.name]: Alert,
    [Form.name]: Form,
    [FormItem.name]: FormItem,
    [Input.name]: Input,
    DynoKeyDetails,
  },
  props: {
    clusterName: {
      type: String,
      required: true,
    },
    keyName: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      isLoading: false,
      isError: false,
      keyValue: undefined as IKeyValue | undefined,
    };
  },
  watch: {
    keyName() {
      this.loadKey();
    },
  },
  created() {
    this.loadKey();
  },
  methods: {
    async loadKey() {
      this.isLoading = true;
      this.isError = false;
      try {
        this.keyValue = await getKey(this.clusterName, this.keyName);
      } catch (err) {
        this.isError = true;
      } finally {
        this.isLoading = false;
      }
    },
    onKeyUpdated() {
      this.loadKey();
    },
  },
});
